.fade-out {
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}

.fade-in {
  opacity: 1;
  transition: opacity 250ms ease-in-out;
}

.hide {
  display: none !important;
}

.show {
  display: block !important;
}

.fade {
  opacity: 0.01;
}

.fixed-width {
  display: inline-block;
  width: 150px;
  font-weight: bold;
}

.muted {
  color: $gray-muted;
}

.no-link {
  text-decoration: underline;
  cursor: pointer;
}

.no-margin {
  margin: 0;
}

.page-header {
  display: block;
  border-bottom: 1px solid $border-color;
  padding-bottom: 10px;
  margin-bottom: 15px !important;
}

.vert-center {
  display: flex;
  align-items: center;
  height: $input-height;
}
