.adtoniq-ga-plugin {
  width: 800px;
  padding: 20px 0;
  font-size: 13px;
  line-height: 1.5;
  font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  color: $text-color;
  * {
    box-sizing: border-box;
  }
  a {
    color: $link-color !important;
    cursor: pointer;
    &:hover {
      color: $link-color-hover !important;
    }
  }
  h2 {
    margin: 0 0 5px;
    font-size: 18px;
  }
  input {
    color: inherit;
    font: inherit;
    margin: 0;
    line-height: normal;
  }
  input[type="button"] {
    -webkit-appearance: button;
    cursor: pointer;
  }
  label {
    display: inline-block;
    max-width: 100%;
    margin-bottom: 5px;
    font-weight: bold;
  }
  small,
  .small {
    font-size: 65%;
  }
}

.adtoniq-logo {
  display: inline-block;
  margin-bottom: 10px;
  width: 250px;
}
